}

.animation-btn {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20.8rem;
  height: 5.0rem;
  color: #333;
  border: 3px solid #333;
  border-radius: 90px;
  /* 112.5% */
  overflow: hidden;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 1px 0 rgba(0, 0, 0, 0.2);
  transition: all 1s ease;
}

.animation-btn:after {
  content: "";
  position: absolute;
  height: 0%;
  left: 50%;
  top: 50%;
  width: 150%;
  z-index: 0;
  transition: all 0.4s ease 0s;
}

.animation-btn span {
  position: relative;
  z-index: 3;
  transition: all 0.4s ease 0s;
}

.animation-btn:hover span {
  text-shadow: none;
}

.animation-btn:hover:after {
  height: 450%;
}

